import { load } from 'redux-localstorage-simple';
import { ADD_TEST } from '../../constants'

const LOCAL_STORAGE = load({ namespace: 'portfolio-app' })
const initialTests = [{
    id: 1,
    question: '–ö–∞–∫–æ–≥–æ —Ü–≤–µ—Ç–∞ –Ω–µ–±–æ?',
    answer: 'b',
    variants: {
        a: '–ö—Ä–∞—Å–Ω–æ–µ',
        b: '–°–∏–Ω–µ–µ',
        c: '–ó–µ–ª–µ–Ω–æ–µ',
        d: '–ë–µ–ª–æ–µ'
    }
},
{
    id: 2,
    question: '–ö–∞–∫–æ–≥–æ —Ü–≤–µ—Ç–∞ —Ç—Ä–∞–≤–∞?',
    answer: 'c',
    variants: {
        a: '–ö—Ä–∞—Å–Ω–æ–µ',
        b: '–°–∏–Ω–µ–µ',
        c: '–ó–µ–ª–µ–Ω–æ–µ',
        d: '–ë–µ–ª–æ–µ'
    }
},
{
    id: 3,
    question: '–ö–∞–∫–æ–≥–æ —Ü–≤–µ—Ç–∞ —Å–æ–ª–Ω—Ü–µ?',
    answer: 'd',
    variants: {
        a: '–ö—Ä–∞—Å–Ω–æ–µ',
        b: '–°–∏–Ω–µ–µ',
        c: '–ó–µ–ª–µ–Ω–æ–µ',
        d: '–ë–µ–ª–æ–µ'
    }
}]

const tests = LOCAL_STORAGE && LOCAL_STORAGE.testing && LOCAL_STORAGE.testing.tests 
? LOCAL_STORAGE.testing.tests 
: initialTests 

let initialState = {
    tests: [ ...tests ]
}

export const testing = (state = initialState, action) => {
    switch (action.type) {

        case ADD_TEST:
            return { ...state, tests: [...state.tests, action.payload] }

        default: return state;
    }
}